import Vue from 'vue'
import Router from 'vue-router'

// 1.登录、注册、主页
import Home from '@/components/home/index'
import Login from '@/components/login/index'
import Register from '@/components/register/index.vue'

// 2.Home的5个导航栏
// 首页
import Shouye from '@/components/home/shouye/index.vue'
// 个人中心
import Person from '@/components/home/person/index.vue'
import Person_a1 from '@/components/home/person/person_a1.vue'
import Person_a2 from '@/components/home/person/person_a2.vue'
import Person_b1 from '@/components/home/person/person_b1.vue'
import Person_c1 from '@/components/home/person/person_c1.vue'
import Person_c2 from '@/components/home/person/person_c2.vue'
import Person_c3 from '@/components/home/person/person_c3.vue'
import Person_c4 from '@/components/home/person/person_c4.vue'
import Person_d1 from '@/components/home/person/person_d1.vue'
import Person_d2 from '@/components/home/person/person_d2.vue'

// 管理中心
import Control from '@/components/home/control/index.vue'
import Control_a from '@/components/home/control/control_a.vue'
import Control_b from '@/components/home/control/control_b.vue'
import Control_c from '@/components/home/control/control_c.vue'
import Control_d from '@/components/home/control/control_d.vue'

// 课程
import Kecheng from '@/components/home//kecheng/index.vue'
import Book_kc from '@/components/home/kecheng/book_kc.vue'

// 资料
import Ziliao from '@/components/home/ziliao/index.vue'
import Book_zl from '@/components/home/ziliao/book_zl.vue'

//3.上传稿件
import Upload from '@/components/upload/index.vue'
import Upload_a from '@/components/upload/upload_a.vue'
import Upload_b from '@/components/upload/upload_b.vue'

Vue.use(Router)

export default new Router({
  // mode:History, 
  routes: [
    {
      path: '/',
      name: '',
      redirect:'/login'
    },
    {
      path: '/upload',
      name: 'Upload',
      component: Upload,
      children: [
        {
          path: '/upload_a',
          name: 'Upload_a',
          component: Upload_a,
        },
        {
          path: '/upload_b',
          name: 'Upload_b',
          component: Upload_b,
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/book_kc',
      name :'Book_kc',
      component: Book_kc,
    },
    {
      path: '/kecheng',
      name :'Kecheng',
      component: Kecheng,
    },
    {
      path: '/ziliao',
      name :'Ziliao',
      component: Ziliao,
    },
    {
      path: '/book_zl',
      name :'Book_zl',
      component: Book_zl,
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
    },
    {
      path: '/home',
      name :'Home',
      component: Home,
      children: [
        {
          path: '/person',
          name :'Person',
          component: Person,
          children: [
            {
              path: '/person_a1',
              name :'Person_a1',
              component: Person_a1,
            },
            {
              path: '/person_a2',
              name :'Person_a2',
              component: Person_a2,
            },
            {
              path: '/person_b1',
              name :'Person_b1',
              component: Person_b1,
            },
            {
              path: '/person_c1',
              name :'Person_c1',
              component: Person_c1,
            },
            {
              path: '/person_c2',
              name :'Person_c2',
              component: Person_c2,
            },
            {
              path: '/person_c3',
              name :'Person_c3',
              component: Person_c3,
            },
            {
              path: '/person_c4',
              name :'Person_c4',
              component: Person_c4,
            },
            {
              path: '/person_d1',
              name :'Person_d1',
              component: Person_d1,
            },
            {
              path: '/person_d2',
              name :'Person_d2',
              component: Person_d2,
            },
          ]
        },
        {
          path: '/control',
          name :'Control',
          component: Control,
          children: [
            {
              path: '/control_a',
              name :'Control_a',
              component: Control_a,
            },{
              path: '/control_b',
              name :'Control_b',
              component: Control_b,
            },{
              path: '/control_c',
              name :'Control_c',
              component: Control_c,
            },{
              path: '/control_d',
              name :'Control_d',
              component: Control_d,
            },
          ]
        },
        {
          path: '/shouye',
          name :'Shouye',
          component: Shouye,
        },
      ]
    }
  ]
})



