<template>
<div>
    <!-- 网页头 -->
    <el-row>
        <el-col :span="5"><el-button type="success">立即参加</el-button></el-col>
        <el-col :span="5" :offset="2"><el-button type="success" icon="el-icon-lollipop">收藏</el-button></el-col>
        <el-col :span="5" :offset="5">欢迎登录，{{user.name}}</el-col>
    </el-row>
    <!-- 视频区 -->
    <el-row class="row_1">
        <el-col :span="15" :offset="2">
            <video src="@/assets/video/a.mp4" width="500px" height="300px" controls></video>
        </el-col>
        <el-col :span="4"  :offset="2">
            <el-menu default-active="1" class="el-menu-vertical-demo" style="width:99%"  unique-opened  @select="handleSelect">
                <el-menu-item index="a">视频1</el-menu-item>
                <el-menu-item index="b">视频2</el-menu-item>
                <el-menu-item index="c">视频3</el-menu-item>
                <el-menu-item index="d">视频4</el-menu-item>
            </el-menu>
        </el-col>
    </el-row>
   
    <!-- 音频区 -->
    <el-row class="row_2"></el-row>
    <!-- 简介区 -->
    <el-row class="row_3">
        <div>
            <h3>简介</h3><h3>简介</h3><h3>简介</h3><h3>简介</h3><h3>简介</h3><h3>简介</h3><h3>简介</h3><h3>简介</h3>
        </div>
    </el-row>
    <!-- 文档区 -->
    <el-row class="row_4">
        <div>
            <h3>文档</h3><h3>文档</h3><h3>文档</h3><h3>文档</h3><h3>文档</h3>
        </div>
    </el-row>
</div>
</template>
<script>
export default {
    methods:{
        handleSelect(index,indexpath){
            console.log(index+'----'+indexpath)
        }
    },
    data(){
        return{
            //通过id、score获取课程具体信息
            id:'',
            score:0,
            videoSrc:'@/assets/video/a.mp4',
            user:{}
        }
    },
    created(){
        console.log("----------------"+this.$route.query.id)
        // this.id='1111'
        // this.score=30
        this.user.name=sessionStorage.getItem("name")
        this.user.auth=sessionStorage.getItem("auth")
        // this.id = sessionStorage.getItem("book_id")
        // this.score = sessionStorage.getItem('book_score')
        this.id=this.$route.query.id
        this.score=this.$route.query.score
        console.log(this.id+this.score)
        // console.log(sessionStorage.getItem("book_id"))
    }
}
</script>
<style scoped>
.row_1{
    background-color: lemonchiffon;
}
.row_2{
    background-color: lightgreen;
}
.row_3{
    background-color: lightgreen;
}
.row_4{
    background-color: lightgreen;
}
</style>


