<template>
<div class="main">
    <el-container >
      <el-header class="headerr">
        <home-nav :headerData="headerData" :logined="logined" 
          :logined_ctrl="logined_ctrl" 
          :userData="userData" @gg="tt"
          :kecheng="kecheng"
          :ziliao="ziliao"></home-nav>
      </el-header>
      <!-- 下句是放aside和main布局 -->
      <!-- <keep-alive> -->
      <el-main>
        <router-view></router-view>
      </el-main>
      <!-- </keep-alive> -->
    </el-container>
</div>
</template>

<style scope>
.main{

}
.el-row{
  width: 100%;
  height: 100%;
}
/* 整个el-header部分 */
.headerr {
    margin: 0%!important;
    width: 100%!important;
    height: 8.3%!important;
    border-radius: 0px!important;
    background-color: aquamarine;
    position: fixed;
    z-index:3;
    /* color: black; */
    /* font-size: 16px; */
    /* background-color: rgb(125, 236, 240); */
    
}
.el-main{
  height: 90%;
  width: 100%;
  background-color: rgb(134, 231, 248);
  margin-top:5%
}
/*  此处设置页面最初头部的image */
</style>

<script>
import HomeNav from '@/components/other/home-nav.vue';

export default {
  components:{
    HomeNav
  },
  created(){
    this.userData={name:sessionStorage.getItem("name"),auth:sessionStorage.getItem("auth")}
    if(this.userData.name==''){
      this.logined=false
      // alert("用户没登录")
    }else{
      this.logined=true
      // alert("欢迎登录！"+"状态："+this.logined)
    }
    if(this.userData.auth=='2'){
      this.logined_ctrl=true
    }
  },
  data () {
    return {
      searchInfo:'',
      userData:{},//登录用户的信息
      logined: false, //判断是否登录，显示个人中心
      logined_ctrl: false,//判断是否为管理员,显示管理中心
      ziliao:true,//资料学习ing
      kecheng:true,//课程学习ing
      activeIndex: '1',
      activeIndex2: '1',
      headerData: {//导航栏菜单
        "首页": "shouye",
      }
    };
  },
  methods:{
    tt(val){
      this.searchInfo = val
      alert("子组件传来的数据是："+this.searchInfo)
    }
  }
}
</script>


