<template>
<div class="main">
    <!-- <el-row style="background-color: aquamarine;"> -->
    <el-col :span="2" >
        <img width="70px" height="55px" src="@/assets/sky.png">
    </el-col>
    <el-col :span="3">
      <br>
      <a href="localhost:8082"  class="aaaa">中国擎天软件微学习平台</a>
    </el-col>

    <el-col :span="2"><br></el-col>
    <el-col :span="10">
        <el-menu :default-active="1+''" mode="horizontal"  router  active-text-color="black" 
            style="background-color: aquamarine;">
            <el-menu-item v-for="(item, index) in headerData"  :key="index" :index="item" >
                <template slot="title">{{index}}</template>
            </el-menu-item>
            <el-menu-item>
            <el-dropdown  @command="handleCommand">
              <span class="el-dropdown-link">
                课程<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <!-- <el-dropdown-item command="kecheng">全部</el-dropdown-item> -->
                <el-dropdown-item command="关于擎天">关于擎天</el-dropdown-item>
                <el-dropdown-item command="产品学习">产品学习</el-dropdown-item> 
                <el-dropdown-item command="技术学习">技术学习</el-dropdown-item>
                <el-dropdown-item command="职业发展">职业发展</el-dropdown-item>
                <el-dropdown-item command="其他">其他</el-dropdown-item>
                    <!-- <el-dropdown-item command="e" divided>蚵仔煎</el-dropdown-item> -->

              </el-dropdown-menu>
            </el-dropdown>
            </el-menu-item>
            <el-menu-item>
            <el-dropdown @command="handleCommand2">
              <span class="el-dropdown-link">
                资料<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <!-- <el-dropdown-item  command="ziliao">全部</el-dropdown-item> -->
                <el-dropdown-item  command="关于擎天">关于擎天</el-dropdown-item>
                <el-dropdown-item  command="产品学习">产品学习</el-dropdown-item>
                <el-dropdown-item  command="技术学习">技术学习</el-dropdown-item>
                <el-dropdown-item  command="职业发展">职业发展</el-dropdown-item>
                <el-dropdown-item  command="其他">其他</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            </el-menu-item>
            <el-menu-item index="person" v-show="logined">个人中心</el-menu-item>
            <el-menu-item index="control" v-show="logined_ctrl">管理中心</el-menu-item>
        </el-menu> 
    </el-col>

    <!-- <el-col :span="5">
      <el-input placeholder="请输入查询内容" v-model="searchInfo">
        <i slot="suffix" class="el-input__icon el-icon-search"  @click.prevent="search"></i>
      </el-input>
    </el-col> -->
    <el-col :span="2">
      <br>
    </el-col>
    <el-col  :span="5" v-show="logined" style="padding-left:50px">
      欢迎登录，{{userData.name}} 
      <el-button type="success" @click="exit">注销</el-button>
    </el-col>
    <el-col :span="5" v-show="!logined">
      <br>
        <a href="http://localhost:8082/#/login">登录</a>|<a href="http://localhost:8082/#/register">注册</a>
    </el-col>

  <!-- </el-row> -->
  </div>
</template>
<script>
export default {
    props:{
        userData:{},
        headerData:{},
        logined: Boolean,
        logined_ctrl:Boolean,
        kecheng:Boolean,
        ziliao:Boolean
    },
    data(){
      return{
        searchInfo:'',
      }
    },
    methods: {
      handleCommand(command){
        sessionStorage.setItem("kc_radio1",command)
        let routeData = this.$router.resolve({path: "/kecheng",query:{kc_radio1:command}})
        window.open(routeData.href,'_blank')
      },
      handleCommand2(command){
        sessionStorage.setItem("zl_radio1",command)
        let routeData = this.$router.resolve({path: "/ziliao",query:{zl_radio1:command}})
        window.open(routeData.href,'_blank')
      },
      test(){
        alert("====")
      },
      search(){
        // alert("搜索的内容是："+this.searchInfo)
        //传给
        this.$emit("gg",this.searchInfo) 
      },
    exit(){
      sessionStorage.removeItem("name")
      sessionStorage.removeItem("auth")
      this.$router.push({ name: "Login" })
    }
  },
}
</script>
<style scoped>
.main{
  height: 8%;
  background-color: aquamarine;
}
.aaaa{
    text-decoration: none;
    /* margin-top:10px */
} 

</style>


