<template>
  <div class="container">
    <div class="logo-header"></div>
    <div class="logo-box"></div>
    <div class="login-box">
      <div class="login-text">
        登 录
        <br />
        <span>欢迎使用考评管理系统</span>
      </div>
      <el-form :model="loginForm"
               ref="loginFormRef"
               :rules="loginFormRules">
        <el-form-item prop="username">
          <el-input v-model="loginForm.username"
                    prefix-icon="el-icon-s-custom"
                    placeholder="请输入用户名"
                    ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="loginForm.password"
                    prefix-icon="el-icon-unlock"
                    placeholder="请输入密码"
                  
                    type="password"></el-input>
        </el-form-item>
        <el-form-item prop="yzm">
          <td>
            <el-input v-model="yzm"
                      prefix-icon="el-icon-unlock"
                      placeholder="输入验证码"
                   
                      type="yzm"
                      style="width: 140px; margin: 0"></el-input>
          </td>
          <td>
            <vue-img-verify id="useYzm"
                            @getImgCode="getImgCode"
                            ref="vueImgVerify" />
          </td>
        </el-form-item>

        <el-form-item class="btns">
          <el-button type="primary"
                     @click="handleLoginForm">登录</el-button>
          <el-button type="primary"
                     @click="loginFormReset">注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import vueImgVerify from "@/components/other/vue-img-verify";
export default {
  data () {
    return {
      // 登录表单数据对象
      yzm: "",
      loginForm: {
        username: "",
        password: "",
      },
      imgCode: "",
      // 登录表单的校验对象
      loginFormRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 20,
            message: "长度在 3 到 20 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  mounted () {
    this.imgCode = this.$refs.vueImgVerify.draw()

  },
  methods: {
    // 多层嵌套无法输入解决方法
    change (e) {
      this.$forceUpdate();
    },

    // 表单重置方法
    loginFormReset () {
      // this.$refs.loginFormRef.resetFields();
      this.$router.push("register")
    },
    // 登录方法
    handleLoginForm () {
      // this.$axios.post("login",this.$qs.stringify({uname:"admin",upwd:"123123"})  )
      //   .then((res)=>{
      //     console.log("验证码："+res.data)
      //   }).catch((error)=>{
      //     console.log(error)
      //   })
      this.$refs.loginFormRef.validate((valid) => {
        // console.log(valid)
        if (!valid) return;
        console.log("验证OK");
      });
     
      if (this.loginForm.username === "admin")
        if (this.loginForm.password === "123")
          if (this.yzm.toLowerCase() === this.imgCode.toLowerCase()) {
            sessionStorage.setItem("vtf", "1")
            this.$store.commit("setUser",{name:this.loginForm.username,auth:'2'})
            this.$router.push({ name: "Home" });

          } else {
            alert("验证码输入错误！");
            this.imgCode = this.$refs.vueImgVerify.draw();
            console.log("验证码: " + this.imgCode);
          }
        else {
          alert("密码输入错误！");
          this.imgCode = this.$refs.vueImgVerify.draw();
          console.log("验证码: " + this.imgCode);
        }
      else {
        alert("用户名输入错误！");
        this.imgCode = this.$refs.vueImgVerify.draw();
        console.log("验证码: " + this.imgCode);
      }
    },
    // 点击图片获取验证码
    getImgCode (code) {
      this.imgCode = code;
      console.log("验证码: " + this.imgCode);
    },
  },
  components: {
    vueImgVerify,
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
  /* background: url("../assets/loginbg2.jpg");
  background-size: 100% 100%; */
}
.logo-header {
  width: 100%;
  top: 0;
  left: 0;
  height: 20%;
  position: absolute;
  /* background: url('../assets/loginbg1.jpg'); */
  background-size: 100% 100%;
}
.logo-box {
  position: absolute;
  width: 60%;
  height: 60%;
  top: 15%;
  left: 20%;
  /* background: url('../assets/loginbg2.jpg'); */
  background-size: 100% 100%;
}
.login-box {
  position: absolute;
  top: 55%;
  left: 70%;
  width: 400px;
  height: 400px;
  transform: translate(-50%, -50%);
  border: 1px solid #ccc;
  border-radius: 5px;
}
.login-text {
  width: 100%;
  font-size: 24px;
  text-align: center;
  color: #2f82fe;
  margin-bottom: 35px;
  box-sizing: border-box;
  padding: 20px;
}
.el-form-item {
  width: 90%;
  margin: 20px auto;
}
.login-text span {
  line-height: 30px;
  font-size: 18px;
  color: #666666;
}
.btns {
  width: 100%;
  text-align: center;
  margin-top: 50px;
}
#useYzm {
  position: absolute;
  margin-left: 28%;
}
</style>


