<template>
<div>
  <div >
      <router-link to='login' style="text-decoration:none">返回主页</router-link>
    </div>

<div class="main">
    
    <div>
        <el-steps :active="active" finish-status="success" align-center>
            <el-step title="步骤 1"></el-step>
            <el-step title="步骤 2"></el-step>
            <el-step title="步骤 3"></el-step>
        </el-steps>
    </div>
   <div v-show="a" style="margin-top:20%;width:40%;margin-left:30%">
    <el-form ref="form" :model="form" label-width="80px" :rules="loginFormRules2">
        <el-form-item label="邮箱" prop="email">
            <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="yanzhengma">
            <el-input v-model="form.yanzhengma" placeholder="请输入验证码">
              <i  slot="suffix" class="el-icon-message" @click.prevent="sendMsg"></i>
            </el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit1">下一步</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
    </div>
    <div v-show="b" style="margin-top:20%;width:40%;margin-left:30%" >
        <el-form ref="form" :model="form" label-width="80px" :rules="loginFormRules1">
        <el-form-item label="用户名" prop="name">
            <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
            <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码">
            <el-input v-model="form.password2" prop="password2"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit2">下一步</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
    </div>
    <div v-show="c" style="margin-top:20%;width:40%;margin-left:30%">
        <h2>恭喜您，注册成功！</h2>
        <el-button type="success" @click="toLogin">现在登录</el-button>
    </div>
    </div>
</div>
</template>
<script>
  export default {
    data() {
      return {
        a:true,
        b:false,
        c:false,
        active:0,
        form: {
          name: '',
          password:'',
          password2:'',
          email:'',
          yanzhengma:''
        },
        yanzhengma:'',//后端返回的验证码
        loginFormRules1: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 20 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
        password2: [
          { required: true, message: "密码确认失败", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
      loginFormRules2: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            min: 3,
            max: 20,
            message: "长度在 3 到 20 个字符",
            trigger: "blur",
          },
        ],
        yanzhengma: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
      }
    },
    methods: {
      toLogin(){
        this.$router.push({path:'login'})
      },
      sendMsg(){
        console.log(this.form)
        this.$axios.post("getCode",this.$qs.stringify({email:this.form.email})  )
        .then((res)=>{
          this.yanzhengma =res.data
          console.log("结果："+res.data)
        }).catch((error)=>{
          console.log(error)
        })
      },
      onSubmit1() {
          if(this.form.yanzhengma==this.yanzhengma){
            // 验证码正确，下一步
            this.a=!this.a
            this.b=!this.b
            this.next()
            console.log('下一步！！！！')
          }else{
            return
          }
          

      },
      onSubmit2() {
        if(this.form.password==this.form.password2&this.form.password!=''&this.form.password2!=''){
          // 两次密码一致,发送请求
          this.$axios.get("register",{email:this.form.email,name:this.form.name,password:this.form.password}).then((res)=>{
            if(res.data == '1'){
              //注册成功
              this.$router.push({path:'login'})
            }else{
              alert("注册失败")
            }
          })
          this.b=!this.b
          this.c=!this.c
          this.next()

          console.log('下一步');
        }else{
          return
        }
          
      },
       next() {
        if (this.active++ > 2) this.active = 0;
      }
    }
  }
</script>
<style scoped>
.main{
    width: 60%;
    margin-left: 20%;
    /* background-color: rgb(74, 216, 235); */
}
.el-input{
    width: 300px;
}
</style>


