<template>
    <div class="book">
        <!-- 封面 -->
        <div>
            <img src="@/assets/logo.png" width="100px" height="100px" @click="test"/>
        </div>
        <!-- 标题 -->
        <div>
            <el-col :span="24">
                {{bookData.name}}
            </el-col>
        </div>
        <!-- 描述 -->
        <div>
            <el-col :span="24">
                {{bookData.text}}
            </el-col>
        </div>
        <!-- 标签 -->
        <div>
            <el-col  :span="24">
                {{bookData.tag}}
            </el-col>
        </div>
        <!-- 播放量、日期 -->
        <footer style="">
            <el-row>
            <el-col :span="12">
                <i class="el-icon-video-play"></i> {{bookData.count}}
            </el-col>
            <el-col :span="12">
                <i class="el-icon-date"></i>{{bookData.date}}
            </el-col>
            </el-row>
        </footer>
    </div>
</template>
<script>
export default {
    props:{
        bookData:{}
    },
    methods:{
        test(){
            // console.log("==")
            //传课程id
            this.$emit("open",{id:this.bookData.id,score:this.bookData.score})
        }
    }
}
</script>
<style scoped>
.book{
    background-color: white;
    width: 300px;
    height: 200px;
}
</style>


