import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        test: '测试',
        user: {
            name: '',
            auth:0,
        }
    },
    getters: {
        
    },
    mutations: {
        setUser(state,val) {
            state.user.name = val.name
            state.user.auth = val.auth
            sessionStorage.setItem("name", val.name)
            sessionStorage.setItem("auth",val.auth)
        }
    }
})


